.header {
  z-index: 9;
  background-color: $black;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 15px 0;
  @include md {
    bottom: auto;
    top: 0;
    padding: 0;
    pointer-events: none; }

  &__logo {
    display: none; }

  &__nav {
    grid-column: 2;
    @include md {
      width: 100%;
      margin: auto 0;
      padding: 20px 0; } }
  &__btn {
    display: flex;
    justify-content: flex-end;
    @include md {
      justify-content: center;
      width: 100%;
      order: 4; }

    .btn {
      width: 100%;
      padding: 0;

      @extend .btn--gradient;
      max-width: 186px;
      @include tablet {
        max-width: 160px; }
      @include md {
        max-width: 220px; }
      &:after,
      &:before {
        border-radius: 10px; } } }

  &__alt-btn {
    display: none;
    position: fixed;
    bottom: 0;
    right: 0;

    z-index: 2;
    pointer-events: auto;
    transition: opacity ease 0.1s;
    @include md {
      &.is-visible {
        display: block; } }

    .btn {
      width: 100%;
      height: 50px;
      border-radius: 0;
      &:hover {
        background: #000; }
      &:after,
      &:before {
        border-radius: 0; } } }

  &__socials {

    display: flex;
    align-items: center;

    height: 50px;
    @include md {
      order: 3; }

    .social {
      gap: 5px;
      &__item {
        &:not(:last-child) {
          margin: 0; } }

      &__link {
        width: 32px;
        height: 32px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 10px;

        &:hover {
          background: $gray; }

        .icon {
          width: 24px;
          height: 24px;
          &-tiktok, &-instagram {
            transform: scale(0.8); } } } } }

  &__inner {
    position: relative;
    display: grid;
    grid-template-columns: 1fr auto 1fr;
    max-width: 1480px;
    padding: 0 20px;
    gap: 20px;
    align-items: center;
    min-height: 52px;
    margin: 0 auto;

    @include md {
      display: none;
      position: fixed;
      top: 0;
      left: 0;
      height: 100svh;
      padding: 20px;
      overflow: hidden;
      overflow-y: auto;
      gap: 30px;
      flex-direction: column;
      justify-content: flex-start;
      width: 100%;
      background-color: $gray;
      z-index: 3; }

    &.is-open {
      @include md {
        pointer-events: auto;
        display: flex; } } }

  &__burger {
    display: none;
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 4;
    @include md {
      display: inline-block;
      pointer-events: auto; } } }

.nav {
  display: flex;
  @include md {
    display: block; }
  &__link {
    text-decoration: none;
    color: $white;
    transition: 0.3s;
    font-size: 16px;
    height: 50px;
    display: flex;
    padding: 0 13px;
    align-items: center;
    justify-content: center;
    border-radius: 10px;

    &:hover {
      color: #9dde00;
      background: $gray; }

    @include md {
      font-size: 14px;
      display: flex;
      text-align: center;
      padding: 10px 20px;
      font-weight: 500; }

    .no-touch &:hover {
      color: $themeColor; } } }

.burger {
  width: 60px;
  height: 60px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @include sm {
    width: 50px;
    height: 50px; }

  span {
    transition: 0.3s;
    display: block;
    height: 4px;
    width: 36px;
    background-color: #ffffff;
    border-radius: 5px;
    &:not(:last-child) {
      margin-bottom: 6px; } }
  &.is-active {
    span {
      &:nth-child(1) {
        transform: translate(0, 10px) rotate(45deg); }
      &:nth-child(2) {
        transform: translateX(0);
        opacity: 0; }
      &:nth-child(3) {
        transform: translate(0, -10px) rotate(-45deg); } } } }
